import './scss/base.scss';
import './scss/fonts.scss';
import validate from 'jquery-validation/dist/jquery.validate.js';
import mask from 'jquery-mask-plugin';
import $ from "jquery";
import "jquery-validation";
import Swiper from 'swiper';
import 'swiper/css';
import { Navigation, Pagination } from 'swiper/modules';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
Swiper.use([Navigation]);

jQuery(document).ready(function($) {

    const categoriesData = {
        kitchens: [
            { img: 'img/picture-1.jpg', title: ' ', subtitle: '', price: '999 ' },
            { img: 'img/picture-1.jpg', title: ' ', subtitle: '', price: '1200 ' },
            { img: 'img/picture-1.jpg', title: ' ', subtitle: '', price: '1050 ' },
            { img: 'img/picture-1.jpg', title: ' ', subtitle: '', price: '950 ' }
        ],
        wardrobes: [
            { img: 'img/picture-1.jpg', title: ' 1', subtitle: '', price: '5000 ' },
            { img: 'img/picture-1.jpg', title: ' 2', subtitle: '', price: '4500 ' },
            { img: 'img/picture-1.jpg', title: ' 3', subtitle: '', price: '4000 ' },
            { img: 'img/picture-1.jpg', title: ' 4', subtitle: '', price: '5500 ' }
        ],
        hallways: [
            { img: 'img/picture-1.jpg', title: ' 1', subtitle: '', price: '1500 ' },
            { img: 'img/picture-1.jpg', title: ' 2', subtitle: '', price: '2000 ' },
            { img: 'img/picture-1.jpg', title: ' 3', subtitle: '', price: '1700 ' },
            { img: 'img/picture-1.jpg', title: ' 4', subtitle: '', price: '1800 ' }
        ],
        wardrobesSliding: [
            { img: 'img/picture-1.jpg', title: '- 1', subtitle: '-', price: '3000 ' },
            { img: 'img/picture-1.jpg', title: '- 2', subtitle: '-', price: '3500 ' },
            { img: 'img/picture-1.jpg', title: '- 3', subtitle: '-', price: '4000 ' },
            { img: 'img/picture-1.jpg', title: '- 4', subtitle: '-', price: '4500 ' }
        ]
    };
    class MyModal {
        constructor($modal, $btn) {
            this._modal = typeof ($modal) === 'string' ? $($modal) : $modal;
            this._closeModal = this._modal.find('.js-close-modal');
            this._body = $('body');
            this._dynamicContent = this._modal.find('.js-modal-dynamic-content');
            this._btn = $btn;

            this._openModal = this._openModal.bind(this);
            this.close = this.close.bind(this);
        }

        init() {
            if(this._modal.length) {
                this._openModal();

                this._closeModal.on('click', this.close);
            }
        }

        _fillDynamicContent() {
            if(!this._dynamicContent.length) {
                return;
            }

            const insertContent = (dataAttr, isVal = false) => {
                if (!isVal) {
                    this._modal.find(`.js-modal-dynamic-content[${dataAttr}]`).text(this._btn.data(`${dataAttr.slice(5)}`));
                } else {
                    this._modal.find(`.js-modal-dynamic-content[${dataAttr}]`).val(this._btn.data(`${dataAttr.slice(5)}`));
                }
            }

            insertContent('data-selected-title');
            insertContent('data-value', true);
        }

        close() {
            $('#imageOverlay').removeClass('active');
            this._modal.removeClass('active');
            this._body.css('overflow', '');
            this._modal.trigger('afterClose');
        }

        _openModal() {
            this._modal.trigger('beforeOpen');
            this._fillDynamicContent();
            this._modal.addClass('active');
            this._body.css('overflow', 'hidden');
        }
    }

// 
    $("form").each(function() {
        $(this).validate({
            rules: {
                username: {
                    required: true,
                    minlength: 3
                },
                phone: {
                    required: true,
                },
                mail: {
                    required: true
                },
            },
            errorClass: "error fail-alert",
            validClass: "valid success-alert",
            errorLabelContainer: $(this).find(".err-container"), //      
            errorElement: "span",
            messages: {
                username: {
                    required: ",   ",
                    minlength: "     3 "
                },
                phone: {
                    required: ",   ",
                },

                mail: {
                    required: ",   . ",
                },
            }
        });
    });

    let swiperInstance = null;
    let currentSlideTitle = ''

    function updateModalContent(category) {
        const categoryData = categoriesData[category];
        Swiper.use([Navigation]);
        if (!categoryData) return;

        const modalContent = $('.js-modal-dynamic-content'); //    
        modalContent.empty(); //   

        categoryData.forEach(item => {
            const slide = `
            <div class="swiper-slide">
                <div class="modal__list-item">
                    <div class="modal__list-pic">
                        <img src="${item.img}" alt="${item.title}">
                    </div>
                    <div class="modal__list-title">${item.title}</div>
                    <div class="modal__list-subtitle">${item.subtitle}</div>
                    <div class="modal__list-price"><span>${item.price}</span></div>
                </div>
            </div>
        `;
            modalContent.append(slide);
        });

        if (swiperInstance) {
            swiperInstance.destroy(true, true); //  
        }

        //    ,   
        swiperInstance = new Swiper('.swiper', {
            slidesPerView: 1,
            spaceBetween: 20,
            loop: true,
            pagination: {
                el: '.swiper-pagination',
                clickable: true,
            },
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
        });

        swiperInstance.on('slideChange', () => {
            const currentSlide = swiperInstance.slides[swiperInstance.activeIndex];
            currentSlideTitle = $(currentSlide).find('.modal__list-title').text();
        });
    }

    function initModal() {
        $(document).on('click', '.js-open-modal', function() {
            const $btn = $(this);
            const modalId = $btn.data('src');
            const category = $btn.data('category');
            const openedModal = new MyModal($(`${modalId}`), $btn);
            openedModal.init();

            updateModalContent(category);

            setTimeout(() => {
                if (!swiperInstance) {
                    swiperInstance = new Swiper('.swiper', {
                        slidesPerView: 1,
                        spaceBetween: 20,
                        loop: true,
                        pagination: {
                            el: '.swiper-pagination',
                            clickable: true,
                        },
                        navigation: {
                            nextEl: '.swiper-button-next',
                            prevEl: '.swiper-button-prev',
                        },
                    });
                } else {
                    swiperInstance.update(); //    ,  
                }
            }, 300); //    
        });
    }
    initModal()

    //        
    $(document).on('click', '.js-order', function() {
        const $modal = $(this).closest('.modal'); //   
        const $commentField = $('#comment'); //   (,  id "comment")

        //       
        $('html, body').animate({
            scrollTop: $commentField.offset().top - 500 //    
        }, 500);

        //       
        if ($commentField.length) {
            $commentField.val(currentSlideTitle);
        }

        //  
        $modal.removeClass('active');

        const $form = $('#main-form');
        if ($form.length) {
            $form.submit(); //   
        }
    });

    $(".header-nav a").on("click", function(event) {
        event.preventDefault(); //    
        const target = $(this).attr("href"); //  ID 
        $("html, body").animate({ scrollTop: $(target).offset().top }, 800); //  
    });

    $(document).on('submit', '.js-checkout', function (e) {
        e.preventDefault(); //    

        const $form = $(this);
        const formData = $form.serialize(); //   

        $.ajax({
            type: "POST",
            url: $form.attr("action"), //  URL   action
            data: formData,
            success: function () {
                const $modalThanks = new MyModal($('#modal-success'), $(document));
                $modalThanks.init(); //   ""
                $form.trigger('reset'); //  
            },
            error: function () {
                alert("   .   .");
            }
        });
    });

    //
    $('.js-phone').mask('+7 (000) 000-00-00', {placeholder: ' '});

    $(document).on('click', '.modal__list-pic img', function() {
        const imgSrc = $(this).attr('src');
        $('#imageOverlay img').attr('src', imgSrc);
        $('#imageOverlay').addClass('active');
    });

//      
    $('#imageOverlay').on('click', function() {
        $(this).removeClass('active');
    });
})